*{list-style:none;text-decoration:none}body,#root{font-family:"Exo 2";letter-spacing:.15em;padding:1rem;display:flex;flex-direction:column;gap:1rem}ul{padding:0}.wrapper-my-projects{position:relative;color:#f5f5f5;background-color:#2d7d88;border-radius:8rem;height:36rem;overflow:hidden;transition:.4s;cursor:pointer}.wrapper-my-projects:hover .button-more-experience{color:rgba(105,185,105,.56);transform:scale(1.1)}.wrapper-my-projects .container-my-projects h3{font-size:2.5rem;text-align:center}.wrapper-my-projects .container-my-projects .list-projects{display:flex;justify-content:center;flex-wrap:wrap;gap:4rem;max-width:1280px;margin:0 auto 4rem auto}.wrapper-my-projects .container-my-projects .list-projects .project{border:dotted #181918 2px;width:20rem;height:20rem;border-radius:30rem;border:2rem #fff solid;background-position:center;background-repeat:no-repeat;background-size:contain;transition:.4s;box-shadow:2rem 1rem 1rem .5rem rgba(0,255,255,.3607843137)}.wrapper-my-projects .container-my-projects .list-projects .project:hover{transform:rotateY(50deg) rotateX(30deg);box-shadow:2rem 2rem 1rem 1rem rgba(105,185,105,.56)}.wrapper-my-projects .container-my-projects .list-projects .project:active{transform:scale(0.9);box-shadow:2rem 2rem 1rem 1rem #000}.wrapper-my-projects .container-my-projects .list-projects .project a{display:block;height:100%}.wrapper-my-projects .button-more-experience{position:absolute;font-size:2.5rem;bottom:.5rem;width:100%;cursor:pointer;text-align:center;transition:.4s}.wrapper-my-projects .button-more-experience:active{transform:scale(0.9)}.wrapper-my-projects .button-more-experience:hover{color:rgba(105,185,105,.56);transform:scale(1.1)}.wrapper-my-projects .button-more-experience::before{position:absolute;width:100%;content:"⮟";left:0;top:-2rem}@media(max-width: 1050px){.wrapper-my-projects{height:32rem}.wrapper-my-projects .container-my-projects h3{font-size:1.5rem}.wrapper-my-projects .button-more-experience{font-size:1rem}}@media(max-width: 850px){.wrapper-my-projects{height:20rem}.wrapper-my-projects .container-my-projects h3{font-size:1.3rem}.wrapper-my-projects .container-my-projects .list-projects .project{border:none;width:12rem;height:12rem}.wrapper-my-projects .button-more-experience{font-size:.6rem}}@media(max-width: 500px){.wrapper-my-projects{height:9rem}.wrapper-my-projects .container-my-projects h3{font-size:.8rem}.wrapper-my-projects .container-my-projects .list-projects .project{border:none;width:5rem;height:5rem}.wrapper-my-projects .button-more-experience{font-size:.6rem}}